<template>
     <div>
        <div class="si_div">
            <span>套餐选择</span>
        </div>
        <div>
            <div class="yuan">
                <span>自定义体检项目</span>
            </div>
        </div>
        <div v-if="me.length>0">
            <ul class="si_list">
               
                <li v-for="mes in me" class="si_item">
                     <router-link :to="{path:'/taoCanXin',query:{id:mes.id}}">
                    <a>{{mes.mealName}}</a>
                    <div class="si_ting">
                        <img src="../assets/physicaldoc.png">
                    </div>
                    </router-link>
                </li>
                
            </ul>
        </div>
        <div class="not" v-else>
          <span>暂无数据</span> 
        </div>
    </div>
</template>

<script>
    export default{
        data () {
            return {
                me:[]
               
            }
        },
        created () {
            this.yuyue();
        },
        methods: {
            yuyue(){

                this.$http.get("http://192.168.3.8:8080/hello/findMeal.json",{})
                .then((response)=>{
                 
                     this.me=response.data.data
                    
                })
            }
        }
    }
</script>

<style>
      .si_ting{
            width: 50px;
            display: inline-block;
            float: right;
        }
        .si_item{
            width: 90%;
            padding: 0px;
            border-bottom: 1px solid #8B8B7A;
            margin-top: 10px;
            overflow: hidden;
        }
        .si_item img{
            width: 20px;
            height: 20px;
        }
        .not{
            width: 300px;
            height: 200px;
        }
        .not span{
            width: 100%;
            display: inline-block;
            line-height: 200px;
            text-align: center;
        }
</style>


