<template>
    <div class="bg">
        <div>
            <ul class="nine_list">
                <li class="nine_item">
                    <input type="text" id="name" placeholder="用户名" />
                </li>
                <li class="nine_item">
                    <input type="password" id="password" placeholder="密码" />
                </li>
            </ul>
        </div>
        <div>
            <div class="ba_div">
                <span v-on:click="denglu">登陆</span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                hao: []
            }
        },
        created() {

        },
        methods: {
            denglu() {
                var name = document.getElementById("name").value;
                var paw = document.getElementById("password").value;
                if (name == "" || paw == "") {
                    alert("请输入用户名或者密码。。");
                    return;
                }
                this.$http.get("http://192.168.3.8:8080/loginName.json", {
                    params: {
                        phone: name,
                        password: paw
                    }


                })
                    .then((response) => {
                        this.hao = response.data.data;
                        if (this.hao = 100) {
                            alert("欧了");
                            this.$router.push({
                                path:'/index'
                            })
                        }


                    }, function (response) {
                        alert(response.data.message);
                    }
                    )
                // Vue.http.interceptors.push(function(request,next){


                //     next((response)=>{
                //         this.hao=response.data.code;
                //         if(this.hao.equals("666")){

                //         }
                //         return response
                //     })
                // })
            }
        }

    }

</script>

<style>

  

    .bg {
        width: 100%;
        height: 100%;
       
      
    }
    .nine_list {
        width: 100%;
        padding: 0px;
    }

    .nine_item {
        line-height: 50px;
    }

    .nine_item input {
        width: 90%;
        height: 25px;
        border: 1px solid #ccc;
        border-radius: 3px;
        margin-left: 5%;
    }
</style>


