<template>
    <div >
        <div class="si_div">
            <span>套餐详情</span>
        </div>
        <div v-for="s in shu.projectItemList">
            <div class="taocan" >
            <ul class="tao_list">
                <li class="tao_item">
                    <span>套餐类型:</span>
                    <span>{{shu.mealName}}</span>
                </li>
                <li class="tao_item">
                    <span>套餐价格:</span>
                    <span>￥{{shu.actualPrice}}</span>
                </li>
                 <li class="tao_item">
                    <span>现金支付价格:</span>
                    <span>￥{{shu.originalPrice}}</span>
                </li>
                 <li class="tao_item">
                    <span>套餐介绍:</span>
                    <span>体检充值卡支付：1000元</span>
                </li>
            </ul>
        </div>
        <div>
            <div>
                <p>体检套餐项目(共{{shu.projectItemList.length}}项)</p>
            </div>
           <div class="pan">
              <ul class="tao_list">
                  <li class="item_tao">
                      <span>{{s.mealId}}.{{s.projectName}}</span>
                    <span>￥{{s.projectPrice}}</span>
                  </li>
                
                </ul>
              <div class="pan_div">
                  <p>{{s.decs}}</p>
              </div>
           </div>
        </div>
        </div>
        
    </div>
</template>

<script>
     export default {
        data() {
            return {
                shu: {},
                num: ""
            }
        },
        created() {
            this.taocan()
        },
        methods: {
            taocan() {
                this.$http.get("/findById.json", {
                    params: {
                        id: this.$route.query.id
                    }
                })
                    .then((response) => {
                        this.shu = response.data.data
                        console.log(this.shu.projectItemList.length)
                    })
            }
        }
    }
</script>

<style scoped>

 

    .taocan{
        width:100%;
        height: 100%;
    }
    .tao_list{
        width: 100%;
        height: 100%;
        margin: 0px;
        padding-left: 10px;
    }
    .tao_item .item_tao{
        width: 100%;
        line-height: 20px;
    }
   .pan{
        width: 100%;
        border-top: 2px solid #ccc;
   }
   .pan_div p{
       margin: 0px;
   }
</style>


