
import xin from './components/xinxi.vue'
import touto from './components/touto.vue'
import ogin from './components/Ogin.vue'
import index from './components/index.vue'
import yuyue from './components/yuyue.vue'
import taocan from './components/taocan.vue'
import taoxin from './components/taoCanXin.vue'

let routes = [
    
{
        path:"/touto",
        component:touto
    },{
        path:'/ogin',
        component:ogin
    },{
        path:'/index',
        component:index
    }
    ,{
        path:'/',
        component:ogin
    },{
        path:'/yuyue',
        name:'yuyue',
        component:yuyue
    },{
        path:'/taocan',
        name:'taocan',
        component:taocan
    },{
        path:'/taoCanXin',
        component:taoxin
    }

]

export default routes;


